@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-size: 14px;
  margin: 0;
  padding: 0;
  font-family: "Rubik", sans-serif;
}

img {
  max-width: 100%;
}

h1 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

.header {
  background-image: url(".../../../images/pattern-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 35vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}
.header__heading {
  color: white;
  font-weight: 500;
  margin: 0.5em 0;
}
.header__input {
  width: 100%;
  padding: 1em;
  border-radius: 1em;
  border: none;
}
.header__input:focus {
  outline: none;
}
.header__form {
  width: 60%;
  display: flex;
  flex-direction: row;
  position: relative;
}
.header__form-img {
  border-radius: 0 1em 1em 0;
  background-color: black;
  position: absolute;
  top: 0;
  right: 0;
  padding: calc(1em + 1.566px);
}
.header__form-img:hover {
  background-color: #2b2b2b;
}

.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background: white;
  border-radius: 10px;
  z-index: 2;
  width: 70%;
  padding: 0;
  position: absolute;
  top: 15%;
  left: 15%;
}
@media screen and (min-width: 920px) {
  .info {
    top: 25%;
    width: 90%;
    left: 5%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    padding: 2.5em 0;
    text-align: left;
  }
  .info div {
    border-right: 2px solid rgba(150, 150, 150, 0.5);
  }
  .info div:nth-child(4) {
    border-right: none;
  }
}
@media screen and (min-width: 1200px) {
  .info {
    width: 70%;
    left: 15%;
  }
}
.info > div {
  padding: 1em;
  margin: 0.5em;
}
.info__label {
  font-size: 0.8em;
  letter-spacing: 0.3em;
  text-transform: uppercase;
  color: #969696;
  margin: 0.5em 0;
}
.info__text {
  font-weight: bold;
  font-size: 1.5em;
}

.map {
  height: 65vh;
  z-index: -2;
}